<template>
  <div id="order-all">
    <order-nav></order-nav>
    <h3>所有人工单</h3>
    <div>
      <el-table :data="allOrderList" border style="width: 90%;margin:0 auto" max-height="600">
        <el-table-column prop="initiate_person" label="发单人"></el-table-column>
        <el-table-column prop="receive_person" label="接单人"></el-table-column>
        <el-table-column prop="receive_department" label="接单部门"></el-table-column>
        <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
        <el-table-column prop="out_time" label="结单时间"></el-table-column>
        <el-table-column prop="need" label="需求"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import orderNav from "@/common/order_nav";
export default {
  name: "",
  props: {},
  components: {
    orderNav
  },
  data() {
    return {
      allOrderList: []
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取所有人工单
    getAllOrderList() {
      this.$api.getAllOrderList().then(res => {
        console.log(res);
        this.allOrderList = res.data.data;
      });
    }
  },
  created() {
    this.getAllOrderList();
  },
  mounted() {}
};
</script>
<style scoped>
#order-all {
  height: 92vh;
  background: rgba(255, 255, 255, 0.7);
}
h3 {
  text-align: center;
}
</style>


